.audio__wrap {
  user-select: none;
  right: 50px;
  position: fixed;
  bottom: 200px;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: #eeee;
  display: flex;
  align-items: center;
  justify-content: center;
  animation: shake 1s infinite;
  border: 1px solid #cccc;
}
